import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../../../auth/_services/user.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';

import { Router, ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { AuthenticationService } from '../../../../../auth/_services/authentication.service';




@Component({
    selector: 'app-vendor-list',
    templateUrl: "vendor-list.component.html",
    styles: []
})
export class VendorListComponent implements OnInit {


    private formSumitAttempt: boolean;

    constructor(private _authService: AuthenticationService, private _dataService: DataService, private router: Router, private toastr: ToastrService, private _userService: UserService) { }
    public userData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    p: number = 1;

    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.customer_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this._dataService.setloaderState(false);
        this._dataService.setactiveMenu('vendor');

        localStorage.setItem('section', 'vendor');

        this._userService.getAll().subscribe(
            data => {
                this.userData = data.success;
                this.noOfPage = data.success.length;
            }
        );
    }




    public search() {

        this.formSumitAttempt = true;
        this._userService.searching({
            search_value: this.searchValue, dropdown_value: this.dropDownValue
        }).subscribe(
            data => {
                //console.log(data);
                this.userData = data.success;
                // this.reset();
            }
            );
    }
    public vendor_delete(del_id) {

        //  console.log(del_id);

        let value = confirm('Are you want to delete this vendor ?');
        // console.log(value);
        if (value) {

            this._userService.del_vendor(del_id).subscribe(
                data => {
                    this.userData = data.data;
                }
            );
        }

    }
    public vendor_logout(user_id) {

        let value = confirm('Are you want to logout this vendor ?');

        if (value) {
            //$value->own
            let currentUser = JSON.parse(localStorage.getItem('currentUser'));
            let input: any;

            if (currentUser.success.id == user_id) {
                input = { 'user_id': user_id, 'is_admin': true, 'own': true };
            } else {
                input = { 'user_id': user_id, 'is_admin': true, 'own': false };
            }

            this._userService.logout_vendor(input).subscribe(
                data => {
                    this.toastr.success(data.success, 'Success Message');
                    if (currentUser.success.id == user_id) {
                        this._authService.logout();
                        this.router.navigate(['/login']);
                    }
                    this.userData = data.data;
                }
            );
        }

    }
    public changeStatus(user: any) {
        console.log(user.user_status);
        if (user.user_status == 'Active') {
            user.user_status = 'Inactive';

            this._userService.updateStaus(user.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            user.user_status = 'Active';
            this._userService.updateStaus(user.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }

    public redirectDetails(user: any) {
        window.location.href = "https://moretolike.com/beta-store-details/" + user.id;
    }
}
